html, body {
    background: url('https://w.wallhaven.cc/full/ox/wallhaven-oxeyr5.jpg') no-repeat center center fixed;
    background-size: cover;
    position: relative;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    }


.navbar {
    font: 12px 'opensans-bold', sans-serif;
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 5px;
    margin: 0 auto;
    height: 10vh;
    width: 100vw;
    background-color: black;
    text-align: center;
 }

h1 {
    text-align: center;
    font-weight: 800;
    font-size: 2em;
    margin: 10px;
    text-transform: uppercase;
    text-shadow: 1px 1px blanchedalmond, 
                2px 2px blanchedalmond, 
                3px 3px blanchedalmond, 
                4px 4px blanchedalmond, 
                5px 5px blanchedalmond;
}
h2 {
    text-align: center;
    font-weight: 800;
    margin-top: 5px;
    font-size: 1.5em;
    text-transform: uppercase;
    text-shadow: 1px 1px blanchedalmond, 
                2px 2px blanchedalmond, 
                3px 3px blanchedalmond, 
                4px 4px blanchedalmond, 
                5px 5px blanchedalmond;
}
h3 {
    text-align: center;
    font-weight: 800;
    font-size: 1em;
}
p {
    font-size: .9em;
    text-align: center;
    font-weight: 700;
}

.contact {
    font-size: 1em;
    text-align: center;
    font-weight: 700;
}
.email {
    font-size: .8em;
    text-align: center;
    font-weight: 700;
}
.navlinks {
    font-size: 15px;
    font-weight: 700;
    color: blanchedalmond;
    padding: 25px 0;
    display: inline-block;
    margin-right: 15px;
    margin-left: 15px;
    text-decoration: none;
}
.navlinks:hover {
    border-bottom:10px blanchedalmond;
    transform: scale(1.1);
    text-shadow: 1px 1px blanchedalmond;
}
.homesection {
    margin:auto;
    display: flex;
    flex-direction: column;
    width: 50%;
}

footer {
    position: absolute;
    bottom: 0;
    background-color: black;
    color: blanchedalmond;
    width: 100%;
    height: 6.5rem;
}
p {
    font-size: 18px;
    font-weight: 700px;
}
.projects {
    position:relative;
    margin-left: auto;
    margin-right: auto;
    width: 50vw;
    height: 50vh;
    background-color: silver;
    border-radius: 20px;
}
.blackjack-container {
    position:relative;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    width: 50vw;
    height: 50vh;
    background-color: silver;
    border-radius: 20px;
}
.current {
    position:relative;
    margin-left: auto;
    margin-right: auto;
    max-width: 65%;
    height: 65%;
    border-radius: 20px;
}
.blackjack-img {
    position:relative;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    max-width: 100%;
    height: 100%;
    border-radius: 20px;
}
.blackjack-image {
    position:relative;
    margin-bottom: auto;
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    max-width: 65%;
    height: 65%;
    border-radius: 20px;
}
@media only screen and (max-width: 550px){
    .blackjack-img { height: 50%; }
}
.current-header{
    padding: 10px;
}

img:hover {
    box-shadow: 0 0 25px 3px black;
    transform: scale(1.1);
  }
.fling-minislide {
    width:450px;
    height:350px; 
    margin-top:55px;
    border-radius: 50%;
    overflow:hidden; 
    position:relative; 
    margin-left: auto;
    margin-right: auto;
    border: solid;
    border-width: 8px;
}
.fling-minislide img {
    position:absolute;
    animation:fling-minislide 20s infinite; 
    opacity:0; 
    width: 100%; 
}
@media only screen and (max-width: 479px){
        .fling-minislide img { 
            width: 200px;
            height: 175px;
            border-radius: 50%;
            text-align: center;
            overflow:hidden; 
 }
 .fling-minislide {
     border: none;
 }
 body {
     overflow-x: hidden;
     overflow-y: scroll;
     width: 100%;
     height: 100%;
 }
 h1 {
     font-size: 95%;
 }
 h2 {
     font-size: 90%;
 }
}
  
@keyframes fling-minislide { 
      25%{opacity:1;} 
      40%{opacity:0;}}
      .fling-minislide img:nth-child(1){animation-delay:15s;}
      .fling-minislide img:nth-child(2){animation-delay:10s;}
      .fling-minislide img:nth-child(3){animation-delay:5s;}
      .fling-minislide img:nth-child(4){animation-delay:0s;}


a {
    color: blanchedalmond;
    text-decoration: none;
    display: inline-block;
    margin: 0 5px;
}

footer div {
    text-align: center;
    
}

.current-header {
    font-weight: 800;
    font-size: 1.5em;
    text-transform: uppercase;
    text-shadow: 1px 1px blanchedalmond, 
                2px 2px blanchedalmond, 
                3px 3px blanchedalmond, 
                4px 4px blanchedalmond, 
                5px 5px blanchedalmond;
}

::-webkit-scrollbar {
    width: 10px;
  }
  ::-webkit-scrollbar-track {
    background: black;
  }
  ::-webkit-scrollbar-thumb {
    background: blanchedalmond;
  }
  ::-webkit-scrollbar-thumb:hover {
  background: #555;
}


